import { canvasBuilder } from '@eswall/canvas'
import { Application, CommandHistory, Wall } from '@eswall/core'
import { PlatformAdapter } from '@eswall/adapters-browser'
import { shortcutManagerBuilder } from '@eswall/shortcuts'
import { Clipboard } from '@eswall/core/src/core/Clipboard'

export function buildApp (container: HTMLDivElement): Application {
	return new Application(
		new Wall([]),
		new CommandHistory(),
		new PlatformAdapter(),
		new Clipboard(),
		canvasBuilder(
			container,
			shortcutManagerBuilder()
		)
	)
}
